@import url(../assets/fonts/Alegreya/alegreya-regular.css);
#application-layout {
  display: grid;
  grid-template-areas: "header" "content" "footer";
  grid-auto-rows: 13% 1fr 70px;
  align-items: center;
  height: 100%; }
  #application-layout header.app-header {
    grid-area: header;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  #application-layout article.app-content {
    grid-area: content;
    justify-content: start;
    height: 100%; }
  #application-layout footer.app-footer {
    height: 100%;
    grid-area: footer;
    display: grid;
    background-color: #5e619d;
    justify-items: center;
    align-items: center; }
