#name-generator {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1em 0; }

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg); } }

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg); } }

#logo {
  font-family: 'Alegreya Regular', sans-serif;
  display: flex;
  align-items: center; }
  #logo .caption {
    font-size: 30px;
    font-weight: bold; }
  #logo svg.rotating {
    -webkit-animation: rotation .6s infinite linear;
            animation: rotation .6s infinite linear; }
  #logo svg.normal {
    -webkit-transform: rotate(200deg);
            transform: rotate(200deg); }

@font-face {
  font-family: 'Alegreya Regular';
  src: url(/quenta/static/media/alegreya_regular.85d55178.eot);
  /* IE 9 Compatibility Mode */
  src: url(/quenta/static/media/alegreya_regular.85d55178.eot?#iefix) format("embedded-opentype"), url(/quenta/static/media/alegreya_regular.70d32ecf.woff2) format("woff2"), url(/quenta/static/media/alegreya_regular.8f262cbc.woff) format("woff"), url(/quenta/static/media/alegreya_regular.0c2c2601.ttf) format("truetype"), url(/quenta/static/media/alegreya_regular.59a17fc9.svg#alegreya_regular) format("svg");
  /* Chrome < 4, Legacy iOS */ }

#application-layout {
  display: grid;
  grid-template-areas: "header" "content" "footer";
  grid-auto-rows: 13% 1fr 70px;
  align-items: center;
  height: 100%; }
  #application-layout header.app-header {
    grid-area: header;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
  #application-layout article.app-content {
    grid-area: content;
    justify-content: start;
    height: 100%; }
  #application-layout footer.app-footer {
    height: 100%;
    grid-area: footer;
    display: grid;
    background-color: #5e619d;
    justify-items: center;
    align-items: center; }

@font-face {
  font-family: 'Lato';
  src: url(/quenta/static/media/Lato-Regular.c5eb5440.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

body {
  margin: 0;
  height: 100vh;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

#root {
  height: 100%; }

